@import url("https://fonts.googleapis.com/css?family=Lato|Open+Sans");
@import url('https://fonts.googleapis.com/css?family=Lato|Merriweather&display=swap');


* {
    box-sizing: border-box;
}

html,
body {

    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;

}
.checked {
    color: orange;
}


#form{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    border-radius: 20px;
    padding: 5vh 2vw;
    min-width: 40vw;
    margin-top: 20px;
    background: white;
    width: 30vw;
    height: 45vh;

}

.heading{
    font-family: 'Merriweather', serif;
    align-self: flex-start;
    font-size: 3.5rem;
    opacity: 1;
    line-height:1.5;
}

.subheading {
    font-family: 'Lato', sans-serif;
    align-self: flex-start;
    font-size: 1.75rem;
    opacity: 0.7;
    line-height:1.5;
}

.paragraph{
    font-family: 'Lato', sans-serif;
    line-height: 1.6;
    font-size: 0.9em;
}


#particles-header {
    position: absolute;
    width: 100%;
    height: 100%;
}


.container {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-evenly;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    margin: 0px;
}

/* this is for the hero*/
.hero {
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: space-between;
    flex-direction: row;
    padding: 32px;
    color: white;

    background: #499bea; /* Old browsers */
    background: -moz-linear-gradient(
            -45deg,
            #499bea 0%,
            #207ce5 100%
    ); /* FF3.6-15 */
    background: -webkit-linear-gradient(
            -45deg,
            #499bea 0%,
            #207ce5 100%
    ); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(
            135deg,
            #499bea 0%,
            #207ce5 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#499bea', endColorstr='#207ce5', GradientType=1); /* IE6-9 fallback on horizontal gradient */
}

.section-40-column{
    width: 40%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.section-60-images{
    width: 60%;
    height: 100%;
}
.section-50-column {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.section-50-images {
    width: 50%;
    height: 100%;
}

.landing-button {
    color: #fff !important;
    text-transform: uppercase;
    background: #FF8361; /* Old browsers */
    padding: 3.5%;
    border-radius: 5px;
    display: inline-block;
    border: none;
    text-decoration: none;
    font-size: 1rem;
}

.landing-button:hover {
    background: #434343;
    letter-spacing: 1px;
    -webkit-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
    -moz-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
    box-shadow: 5px 40px 10px rgba(0, 0, 0, 0.57);
    transition: all 0.4s ease 0s;
}

/*hero ends here*/

/*this is the card section for steps*/
.card-section {
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    flex-wrap: wrap;
    padding-bottom: 16px;
    align-items: center;
    margin: 16px;
}

.card {
    /* Add shadows to create the "card" effect */
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    text-align: center;
    border-radius: 20px;
    padding: 5vh 2vw;
    min-width: 40vw;
    margin-top: 20px;
    background: white;
    width: 30%;
    height: 45vh;
    align-self: center;
    align-items: center;

}

.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

/*end of card section*/


.text-section {
    background: rgb(35, 125, 235);
    background: -webkit-linear-gradient(
            160deg,
            rgb(10, 60, 180) 0%,
            rgb(35, 125, 235) 100%
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
            160deg,
            rgb(10, 60, 180) 0%,
            rgb(35, 125, 235) 100%
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    padding: 75px 32px;
    display: flex;
    flex-direction: row;
    color: white;
    justify-content: space-between;
    align-items: center;
}


.testimonials {
    display: flex;
    flex-direction: column;
    background: rgba(223, 223, 223, 0.65);
    flex-wrap: wrap;
    width: 100%;
    padding: 24px;
    justify-content: center;
    align-items: center;
}

.testimonials-cards-section {
    margin-top: 16px;
    padding-top: 32px;
    margin: 0 auto;
    padding: 40px;
    width: 100%;

}

.testimonial-card {
    justify-content: center;
    background: white;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); */
    /* border-radius: 20px; */
    height: auto;
    /* margin-bottom: 20px; */
    width: 90% !important;
    /* height: 60vh; */
    margin: 20px;
    padding: 5vh 1.5vw;
    text-align: center;
    font-family: "Open Sans", sans-serif;
}

.testimonial-card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}


footer {
    background-color: #3d3d3d;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* ======== BREAK POINTS ========= */

@media screen and (max-width: 920px) {

    #particles-header {
        display: none;
    }

    .hero {
        height: 85vh;
        padding: 0px 32px;
    }

    .section-50-column {
        width: 100%;
        align-self: center;
    }

    .section-40-column {
        width: 100%;
        align-self: center;
    }


    .section-60-images{
        display: none;
    }
    .section-50-images {
        display: none;
    }

    .card {
        width: 100%;
        height: auto;
    }

    .heading {
        font-size: 2rem;
    }

    .subheading {
        font-size: 1.3rem;
    }

    .landing-button {
        font-size: 1rem;
    }

    .hero {
        align-items: center;
        justify-content: center;
    }


    .testimonial-card {
        width: 100%;
        /* height: 70vh */
    }

    .para{

        height: 18vh;

    }
}
